import { useEffect, useState, useRef } from "react";

import Card from 'react-bootstrap/Card';
import TimerControls from './TimerControls';
import TimerOptions from "./TimeOptions";
import VisualTimer from "./VisualTimer";

function TimerCard(props) {

  


    // const [timerTime, setTimerTime] = useState(props.pomodoroTime);
    const [isPaused, setIsPaused] = useState(true);
    const [needToResetTimer, setNeedToResetTimer] = useState(false);
    const [timerType, setTimerType] = useState('pomodoro');

    const prevPomodoroTime = usePrevious(props.pomodoroTime);
    const prevShortBreakTime = usePrevious(props.shortBreakTime);
    const prevLongBreakTime = usePrevious(props.longBreakTime);

    const TimerTypes = {
      pomodoro: 'pomodoro',
      shortBreak: 'shortBreak',
      longBreak: 'longBreak'
    }

    


    

    var TimerLengths = {
      pomodoro: (props.pomodoroTime * 60),
      shortBreak: (props.shortBreakTime * 60),
      longBreak: (props.longBreakTime * 60)
    }

    var updateTimerType= function(type){
      setTimerType(type)
    }

    var [timer, setTimer] = useState();

    var togglePause = function(){
      setIsPaused(!isPaused)
    }

    var restartTimer = function(){
      setNeedToResetTimer(true);
      setIsPaused(true);
    }

    function usePrevious(value) {
      const ref = useRef();
      useEffect(() => {
        ref.current = value;
      });
      return ref.current;
    }

    useEffect( ()=>{

      console.log("updating pomodoro time: "+props.pomodoroTime)
      
      if(timerType === 'pomodoro'){

        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("SET-TIMER_"+(props.timerTime + ((props.pomodoroTime - prevPomodoroTime)* 60)));
        });

        // clearTimeout(timer);
        props.setTimerTime(props.timerTime + ((props.pomodoroTime - prevPomodoroTime)* 60))
      }
    },[props.pomodoroTime])

    useEffect( ()=>{
      
      console.log("updating short break time: "+props.shortBreakTime)

      if(timerType === 'shortBreak'){

        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("SET-TIMER_"+(props.timerTime + ((props.shortBreakTime - prevShortBreakTime)* 60)));
        });

        // clearTimeout(timer);
        props.setTimerTime(props.timerTime + ((props.shortBreakTime - prevShortBreakTime)* 60))
      }
    },[props.shortBreakTime])

    useEffect( ()=>{

      console.log("updating long break time: "+props.longBreakTime)
      
      if(timerType === 'longBreak'){
  
        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("SET-TIMER_"+ (props.timerTime + ((props.longBreakTime - prevLongBreakTime)* 60)) );
        });

        // clearTimeout(timer);
        props.setTimerTime(props.timerTime + ((props.longBreakTime - prevLongBreakTime)* 60))
      }
    },[props.longBreakTime])

    useEffect( ()=>{
      console.log("timerType: "+timerType)

      navigator.serviceWorker.ready.then( registration => {
        registration.active.postMessage("STOP-TIMER");
      });

      navigator.serviceWorker.ready.then( registration => {
        registration.active.postMessage("SET-TIMER_"+TimerLengths[timerType]);
      });

      props.setTimerTime(TimerLengths[timerType])
      setIsPaused(true);
    },[timerType]);

    // useEffect( ()=>{
    //   if (needToResetTimer){ 
    //     // props.setTimerTime(TimerLengths[timerType]);
    //     navigator.serviceWorker.ready.then( registration => {
    //       registration.active.postMessage("SET-TIMER_"+TimerLengths[timerType]);
    //     });
    //     setNeedToResetTimer(false);
    //   }
    //   if ((props.timerTime <= 0) || (isPaused)){
    //     // clearTimeout(timer);
    //     navigator.serviceWorker.ready.then( registration => {
    //       registration.active.postMessage("STOP-TIMER");
    //     });
    //     if (props.timerTime <= 0 && (!needToResetTimer)){
    //       if (timerType === TimerTypes.pomodoro){
    //         setTimerType(TimerTypes.shortBreak)
    //       }else{
    //         setTimerType(TimerTypes.pomodoro)
    //       }
          
    //     }
    //   }else{
    //     // console.log("updating time")
    //     console.log("Changing Time...")
    //     navigator.serviceWorker.ready.then( registration => {
    //       registration.active.postMessage("START-TIMER");
    //     });

    //     // setTimer(setTimeout( () => {
    //     //   props.setTimerTime(props.timerTime-1)
    //     // }, 1000)) 
    //   }
    // }, [props.timerTime, isPaused, needToResetTimer]);

    useEffect( ()=>{

    },[props.timerTime]);

    useEffect( ()=>{

      console.log("isPaused: "+isPaused)
      if (isPaused){
        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("STOP-TIMER");
        });
      }else{
        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("START-TIMER");
        });
      }
    },[isPaused]);

    useEffect( ()=>{

      console.log("needToResetTimer: "+needToResetTimer)

      if (needToResetTimer){
        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("STOP-TIMER");
        });

        console.log("restart Time: "+TimerLengths[timerType])
        navigator.serviceWorker.ready.then( registration => {
          registration.active.postMessage("SET-TIMER_"+TimerLengths[timerType]);
        });

        setNeedToResetTimer(false);
      }
      
    },[needToResetTimer]);


    return (
      <div className="TimerCard" style={{ textAlign: "center" }}>
        
        <Card>
            <TimerOptions updateTimerType={updateTimerType} timerType={timerType} TimerTypes={TimerTypes}/>
            <Card.Body>
                <VisualTimer time={props.timerTime}/>
                <TimerControls togglePause={togglePause} restartTimer={restartTimer} isPaused={isPaused} />
            </Card.Body>
        </Card>
      </div>
    );
  }
  
  export default TimerCard;